.profile__about {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 336px;
}
@media screen and (max-width: 780px) {
  .profile__about {
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    max-width: 282px;
  }
}
